import { Injectable } from '@angular/core';

@Injectable()
export class ValidateService {

  constructor() { }

  validateRegister(user){
    if(user.username == undefined || user.password == undefined){
      return false;
    } else {
      return true;
    }
  }

 validateLogin(user){
    if(user.username == undefined || user.password == undefined){
      return false;
    } else {
      return true;
    }
 }




}



